<template>
<div>
	<div class="cart">
       <div class="cart-left">
            <!-- 购物车 -->
            <!-- 点击购物车出现列表 -->
            <div class="gouwuche" :class="{'isCanClick':totalCounts>0,'isNotCanClick':totalCounts==0}" @click="showOrHiddenCartDetail">
                 <i class="iconfont icon-gouwuche"></i>
            </div>
            <!-- 购物车上面的数字 -->
            <div class="totalCounts" :style="totalCounts<=0?'display:none':'display:block'">
                 <span>{{totalCounts}}</span>
            </div>
            <div class="price" :class="{'isZone':totalPrice==0,'isNotZone':totalPrice!=0}">&yen;{{totalPrice}}</div>
        </div>
        <div class="cart-middle">
	        <div></div>
            <div class="desc">另需配送费&yen;{{getSellers.deliveryPrice}}元</div>
        </div>
        <div class="cart-right">
            <div class="pay" :class='{"enough":totalPrice >= getSellers.minPrice}'>
		        {{pay}}
	        </div>
        </div>
    </div>
     <transition enter-active-class="animated flipInY"  leave-active-class="animated flipOutY">  
	    <cart-detail v-if="isClick&&totalCounts>0"></cart-detail>
	</transition>
</div>
</template>
<script>
    import {mapGetters} from "vuex"
    import cartDetail from "./cartDetail.vue"
	export default{
		data(){
			return {
				isClick:false,
			}
		},
		computed:{
			...mapGetters(["getGoods","getSellers"]),
            getCartGoods(){
            	var getCartGoods=[];
            	this.getGoods.forEach((values)=> {
            		values.foods.forEach((value)=>{
	            		if(value.counts>0){
	            			getCartGoods.push(value);
	            		}
            		}) 
            	});
            	return getCartGoods;
            },
			totalCounts(){
				var counts=0;
				this.getCartGoods.forEach((value)=>{
                     counts+=value.counts;

				});
				return counts;
			},
			totalPrice(){
				var price=0;
				this.getCartGoods.forEach((value)=>{
                     price+=value.price*value.counts;
				});
				return price;
			},
			 pay(){
                if(this.totalPrice === 0 ){
                    return `￥${this.getSellers.minPrice}元起送`
                }else if( this.totalPrice < this.getSellers.minPrice ){
                    return `还差${this.getSellers.minPrice - this.totalPrice}元起送`
                }else{
                    return `去结算￥${this.totalPrice+this.getSellers.deliveryPrice}`
                }

            }
		},
		methods:{
			showOrHiddenCartDetail(){
				 if(this.totalCounts>0){
				     this.isClick=!this.isClick;
					 if(this.isClick){
			             this.$store.dispatch("showCartGoodsDetail");
			             return;
					 }
					 this.$store.dispatch("hiddenCartGoodsDetail");
				}
		    }
		},
		components:{
            cartDetail,
		},
	}
</script>
<style lang="less" scoped> 
	.cart{
		z-index:9;
		display:flex;
		align-items:center;
		justify-content:space-around;
		position:fixed;
		bottom:0;
		background-color: #141d27;
		height:0.92rem;
		width:100%;
		.cart-left{
			display:flex;
			align-items:center;
			.gouwuche{
				line-height: 0.88rem;
				text-align: center;
				height:0.88rem;
				width:0.88rem;
				border-radius: 50% ;
				background-color:#2b343c;
				border: 0.12rem solid  #141d27;
				margin-bottom:0.16rem;
			}
			.isCanClick{
				background-color:rgb(0,160,220);
			}
			.isNotCanClick{
				background-color:#2b343c;
			}
			.icon-gouwuche{
				font-size: 0.48rem;
				color:rgba(255,255,255,0.4);
				line-height: 0.48rem;
			}
			.totalCounts{
				background: rgb(240,20,20);
				padding:0.01rem 0.12rem;
				font-size: 0.18rem;
				position:absolute;
				left:0.88rem;
				top:-0.22rem;
				font-weight: 700;
				line-height: 0.32rem;
				text-align: center;
				color:rgb(255,255,255);
				border-radius: 0.16rem;
				box-shadow: 0 0.04rem 0.08rem 0 rgba(0,0,0,0.4);
			}
			.price{
				padding:0 0.36rem 0 0.24rem;
				font-size: 0.32rem;
				font-weight: 700;
				line-height: 0.48rem;
			}
			.isZone{
				color:rgba(255,255,255,0.4);
			}
			.isNotZone{
				color:rgb(255,255,255);
			}
		}
		.cart-middle{
			.desc{
				font-size: 0.32rem;
				font-weight: 700;
				line-height: 0.48rem;
				color:rgba(255,255,255,0.4);
			}
		}
		.cart-right{
			.pay{
				font-size: 0.24rem;
				font-weight: 700;
				line-height: 0.48rem;
				color:rgba(255,255,255,0.4);
			}
			.enough{
                color:rgb(0,160,220);
            }
		}
	}
</style>


