<template>
<div>
	<div class="ratings">
			<div>
				商品评价
			</div>
			<div>
				<div @click="clickRatings($event,'all')">
					<slot name="all"></slot>
					<span>{{allRatings.length}}</span>
				</div>
				<div @click="clickRatings($event,'good')">
					<slot name="good"></slot>
					<span>{{goodRatings.length}}</span>
				</div>
				<div @click="clickRatings($event,'bad')">
					<slot name="bad"></slot>
					<span>{{badRatings.length}}</span>
				</div>
			</div>
			<!-- 线 -->
			<div></div>
	</div>
	<div class="only-content-ratings" @click="selectHasContentRatings($event)">
			<div>
			   <i class="iconfont icon-duihao" :class="{click:isClick,notClick:!isClick}"></i>
			</div>
			<div class="content">
					只看有内容的评价
			</div>
	</div>
</div>
</template>
<script>
	export default{
		data(){
			return{
				// 是否点击了只看有内容的评价
				isClick:false,
				// 当前点击页
				// 0代表全部
				// 1代表推荐或者满意
				// 2代表吐槽或者不满意
				// 默认是全部
				index:0,
			}
		},
		computed:{
		  // rateType 和 text 的过滤
	      allRatings(){
	      	 var allRatings=[];
	         return this.goodsRatings;
	      },
	      allRatingsHasContent(){
	      	 var allRatingsHasContent=[];
	         this.allRatings.forEach((value)=>{
               if(value.text!==""){
	                 allRatingsHasContent.push(value);
	              }
			});
	         return  allRatingsHasContent;
	      },
	      goodRatings(){
	         var goodRatings=[];
		     this.goodsRatings.forEach((value)=>{
		         if(value.rateType===0){
		                 goodRatings.push(value);
		            }
		      });
	          return goodRatings;
	      },
	      goodRatingsHasContent(){
	         var goodRatingsHasContent=[];
		     this.goodRatings.forEach((value)=>{
		         if(value.text!==""){
		                 goodRatingsHasContent.push(value);
		            }
		      });
	          return goodRatingsHasContent;
	      },
	      badRatings(){
	          var badRatings=[];
		      this.goodsRatings.forEach((value)=>{
		          if(value.rateType===1){
		                 badRatings.push(value);
		              }
		          });
	          return badRatings;
	      },
	      badRatingsHasContent(){
	         var badRatingsHasContent=[];
		     this.badRatings.forEach((value)=>{
		         if(value.text!==""){
		                 badRatingsHasContent.push(value);
		            }
		      });
	          return badRatingsHasContent;
	      },
		},
		props:["goodsRatings"],
		methods:{
			clickRatings(e,type){
                if(!e._constructed){
                	console.log('++++++++');
            		return;
            	}
            	switch (type) {
            		// 点击了全部
            		case "all":
            		    this.index=0;
            		    if(this.isClick){
                            this.$emit("send",this.allRatingsHasContent);
            		    }else{
	            		    this.$emit("send",this.allRatings);
            		    }
            			break;
            		// 点击了推荐或者满意
            		case "good":
            		    this.index=1;
            		    if(this.isClick){
                            this.$emit("send",this.goodRatingsHasContent);
            		    }else{
	            		    this.$emit("send",this.goodRatings);
            		    }
            			break;
            		// 点击了吐槽或者不满意
            		case "bad":
            		    this.index=2;
            		    if(this.isClick){
                            this.$emit("send",this.badRatingsHasContent);
            		    }else{
	            		    this.$emit("send",this.badRatings);
            		    }
            			break;
            		default:
            			// statements_def
            			break;
            	}
			},
            selectHasContentRatings(e){
            	if(!e._constructed){
            		return;
            	}
            	this.isClick=!this.isClick;
            	switch (this.index) {
            		// 当前页是全部
            		case 0:
            			var allFilterRatingsSelect=[];
            			if(this.isClick){
            				this.allRatings.forEach((value)=>{
                               if(value.text!==""){
					                 allFilterRatingsSelect.push(value);
					              }
            				});
            			}else{
            				allFilterRatingsSelect=this.allRatings;
            			}
            			this.$emit("send",allFilterRatingsSelect);
            			break;
            		// 当前页是推荐或者满意
            		case 1:
            			var goodFilterRatingsSelect=[];
            			if(this.isClick){
            				this.goodRatings.forEach((value)=>{
                               if(value.text!==""){
					                 goodFilterRatingsSelect.push(value);
					              }
            				});
            			}else{
            				goodFilterRatingsSelect=this.goodRatings;
            			}
            			this.$emit("send",goodFilterRatingsSelect);
            			break;
            	    // 当前页是吐槽或者不满意
            		case 2:
            			var badFilterRatingsSelect=[];
            			if(this.isClick){
            				this.badRatings.forEach((value)=>{
                               if(value.text!==""){
					                 badFilterRatingsSelect.push(value);
					              }
            				});
            			}else{
            				badFilterRatingsSelect=this.badRatings;
            			}
            			this.$emit("send",badFilterRatingsSelect);
            			break;
            		default:
            			// statements_def
            			break;
            	}
            }
		}
	}
</script>
<style lang="less" scoped>
 .ratings{
		padding:0.36rem 0.36rem 0 ;
		>div:nth-child(1){
			font-size: 0.28rem;
	        font-weight: 700;
	        line-height: 0.28rem;
	        color:rgba(7,17,27,0.8);
			margin-bottom: 0.36rem;
		}
		>div:nth-child(2){
			display: flex;
			>div:nth-child(1),>div:nth-child(2),>div:nth-child(3){
				border-radius: 0.2rem;
                padding:0.16rem 0.24rem;
			}
			>div:nth-child(1){
				background-color: rgb(0,160,220);
				/*全部*/
			    >span:nth-child(1),>span:nth-child(2){
	                	color:rgb(255,255,255);
	                	line-height: 0.32rem;
			       	}
		       	>span:nth-child(1){
	                	font-size: 0.24rem;
			       	}
		       	 >span:nth-child(2){
			       	 	font-size: 0.16rem;
			       	}
			}
			>div:nth-child(2){
                margin-left:0.16rem;
                background-color: rgba(0,160,220,0.2);
                /*推荐*/
                >span:nth-child(1),>span:nth-child(2){
	                	color:rgb(77,85,93);
	                	line-height: 0.32rem;
			       	}
		       	>span:nth-child(1){
	                	font-size: 0.24rem;
			       	}
		       	>span:nth-child(2){
			       	 	font-size: 0.16rem;
			       	}
			}
			>div:nth-child(3){
				margin-left:0.16rem;
				background-color: rgba(77,85,93,0.2);
				/*吐槽*/
				>span:nth-child(1),>span:nth-child(2){
	                	color:rgb(77,85,93);
	                	line-height: 0.32rem;
			       	}
		       	>span:nth-child(1){
	                	font-size: 0.24rem;
			       	}
		       	>span:nth-child(2){
			       	 	font-size: 0.16rem;
			       	}
			}
			
		}
		/*
		 线
		*/
		>div:nth-child(3){
			background-color:rgba(7,17,27,0.1);
	        height:0.02rem;
	        margin-top: 0.36rem;
		}
	}
 .only-content-ratings{
	  	display: flex;
	  	align-items:center;
	  	padding:0.24rem 0.36rem;
	  	border-bottom: 0.02rem solid rgba(7,17,27,0.1);
	  	.icon-duihao,.content{
	  	 	line-height: 0.48rem;
	  	}
	  	.icon-duihao{
	  	 	font-size:0.48rem;
	  	}
	  	.click{
	        color:rgb(0,160,220);
	  	}
	  	.notClick{
	        color:rgb(147,153,159);
	  	}
	  	.content{
	  	 	color:rgb(147,153,159);
	  	 	font-size:0.24rem;
	  	 	margin-left: 0.08rem;
	  	}
	}
</style>


