<template>
    <div class="plus-minus-counts">
    <transition enter-active-class="animated bounce"  leave-active-class="animated bounce">  
      <div @click.stop="minus($event)" v-show="value.counts">
      <i class="iconfont icon-jianhao" ></i>
      </div>
    </transition>
      <div class="counts" v-show="value.counts">
        {{value.counts}}
      </div>
    	<div  @click.stop="plus($event)">
			<i class="iconfont icon-jiahao"></i>
		</div>
    </div>
</template>
<script>
  export default{
		props:["value"],
		methods:{
			 plus(e){
              if(!e._constructed) {
                    return;
                }
              if(this.value.counts){
                 this.value.counts++;
              }else{
                  this.$set(this.value,"counts",1);
              }
        },
        minus(e){
              if(!e._constructed) {
                    return;
                }
              this.value.counts--;
          }
    		}
	}
</script>
<style lang="less" scoped>
  .plus-minus-counts{
  	display: flex;
  	align-items:center;
  	.counts{
  		font-size: 0.2rem;
  		color:rgb(147,153,159);
  		line-height: 0.48rem;
  		padding:0 0.12rem;
  	}
  	.icon-jianhao,.icon-jiahao{
  		color:rgb(0,160,220);
  		font-size: 0.48rem;
  		line-height: 0.48rem;
  	}
  }
	
</style>


