<template>
   <div class="goodsDetailBackground">
	<!--最上面的图片  -->
	<div ref="goodsDetail" class="wrap">
		<div>
			<div class="imgs">
			    <img :src="goodsDetail.image" alt="" class="img">
			</div>
			<div class="back" @click="back($event)">
					<i class="iconfont icon-shiliangzhinengduixiang4"></i>
			</div>
			<div class="content">
	            <div>{{goodsDetail.name}}</div>
	            <div>
                   <div>月售{{goodsDetail.sellCount}}份</div>
                   <div>好评率{{goodsDetail.rating}}%</div>
	            </div>
	            <div class="addCart">
		            <div>
		            	<div>
		                     <span>
		            	    	&yen;
		            	    </span>
		            	    <span>
			            		{{goodsDetail.price}}
		            	    </span>
		            	</div>
		            	<div v-if="goodsDetail.oldPrice">
			            	<del>
			            	    <span>
			            	    	&yen;
			            	    </span>
			            	    <span>
				            		{{goodsDetail.oldPrice}}
			            	    </span>
			            	</del>
		            	</div>
		            </div>
		            <div>
						<plus-or-minus :value="goodsDetail" v-if ="goodsDetail.counts"></plus-or-minus>
					    <div class="addToCart" @click="addToCart($event)" v-if ="!goodsDetail.counts">
						加入购物车
						</div>
		            </div>
		        </div>
			</div>
			<div class="split"></div>
			<!-- 商品介绍 -->
			<div class="info">
			    <div>商品介绍</div>
				<div>
					{{goodsDetail.info}}
				</div>
			</div>
			<div class="split"></div>
			<click-and-select-ratings :goodsRatings="goodsDetail.ratings" @send="sendData">
				<span slot="all">全部</span>
				<span slot="good">推荐</span>
				<span slot="bad">吐槽</span>
			</click-and-select-ratings>
			<goods-ratings-template v-for="(value,key) in  isClickSelect?getSelectRatings:goodsDetail.ratings " :key="key" :goodsRatingsTemplate="value"></goods-ratings-template>
		</div>
   </div>
 </div>
</template>
<script>
	import {mapGetters} from "vuex"
	import BScroll  from "better-scroll"
	import plusOrMinus from "./plusOrminus"
	// 点击以及选择有内容组件
	import clickAndSelectRatings from "./clickAndSelectRatings"
	// 评价组件
	import goodsRatingsTemplate from "./goodsRatingsTemplate"
	export default{
		data(){
			return{
				ratings:{},
				isClickSelect:false,
				getSelectRatings:[]
			}
		},
		props:["goodsDetail"],
		components:{
			plusOrMinus,
			goodsRatingsTemplate,
			clickAndSelectRatings
		},
		created(){
			this.$nextTick(()=>{
				this.initScroll();
			})
		},
		methods:{
			initScroll(){
				setTimeout(()=>{
                this.ratings=new BScroll(this.$refs.goodsDetail,{
                    probeType:3,
                    click:true
                 });
				},0)
			},
			back(e){
				if(!e._constructed){
					return;
				}
                this.$store.dispatch("hiddenGoodsDetail");
			},
			addToCart(e){
				if(!e._constructed){
					return;
				}
                this.$set(this.goodsDetail,"counts",1);
			},
			sendData(data){
				  this.isClickSelect=true;
		          this.getSelectRatings=data;
		          this.$nextTick(()=>{
		          this.ratings.refresh();
		         });
			}
			
		}
	}
</script>
<style lang="less" scoped>
.goodsDetailBackground{
	position: fixed;
	background:rgb(255,255,255);
	top:0;
	bottom: 0.92rem;
	left:0;
	right:0;
	.wrap{
		/*
		这里的高度有问题
		*/
		height: 100%;
	    overflow: hidden;
	.imgs{
	    width: 100%;
	    height: 0;
	    position: relative;
	    padding-top: 100%;
	    .img{
	        position: absolute;
	        top: 0;
	        width: 100%;
	        height: 100%;
	        left: 0;
	        }
	    }
	.back{
		position: fixed;
		top:10px;
		left: 10px;
		width: 10px;
		height: 10px;
		padding:10px;
		color: white;
		}
	.content{
		padding:0.36rem;
		>div:nth-child(1){
           font-size: 0.28rem;
           line-height: 0.28rem;
           font-weight: 700;
           color:rgb(7,17,27);
		}
		>div:nth-child(2){
	       margin-top: 0.16rem;
	       display: flex;
	       >div:nth-child(1),>div:nth-child(2){
		       	font-size: 0.2rem;
		       	line-height: 0.2rem;
		       	color:rgb(147,153,159);
	       }
	       >div:nth-child(2){
	       	     margin-left:0.24rem;
	       }
		}
		.addCart{
		  margin-top: 0.36rem;
		  display: flex;
          align-items:center;
          justify-content:space-between;
		  >div:nth-child(1){
			    display: flex;
			    >div:nth-child(1){
	                >span:nth-child(1),>span:nth-child(2){
	                	color:rgb(240,20,20);
	                	line-height: 0.48rem;
			       	}
			       	>span:nth-child(1){
			       		font-size: 0.2rem;
	                    font-weight: normal;
			       	}
			       	>span:nth-child(2){
			       		font-size: 0.28rem;
	                    font-weight: 700;
			       	}
		       }
		       >div:nth-child(2){
			       	margin-left: 0.24rem;
		       	    del{
		       	   	    >span:nth-child(1),>span:nth-child(2){
		                	color:rgb(147,153,159);
		                	line-height: 0.48rem;
		                	font-size: 0.2rem;
				       	}
			       	    >span:nth-child(1){
		                    font-weight: normal;
				       	}
			       	    >span:nth-child(2){
		                    font-weight: 700;
				       	}
		       	   }
		       }
			}
		 >div:nth-child(2){
		 	.addToCart{
	            border-radius: 0.24rem;
	            background-color: rgb(0,160,220);
	            font-size: 0.2rem;
	            line-height: 0.24rem;
	            color:rgb(255,255,255);
	            padding: 0.2rem 0.24rem;
		 	}
		 }
	   }
		
	}
	.split{
        background-color: #f3f5f7;
        height:0.32rem;
        border-top: 0.03rem solid rgba(7,17,27,0.1);
        border-bottom: 0.03rem solid rgba(7,17,27,0.1);
	}
	.info{
		padding:0.36rem;
		>div:nth-child(1){
             font-size: 0.28rem;
	         font-weight: 700;
	         line-height: 0.28rem;
	         color:rgba(7,17,27,0.8);
		}
		>div:nth-child(2){
			 margin:0.12rem 0.16rem 0;
			 font-size: 0.24rem;
	         font-weight: 200;
	         line-height: 0.48rem;
	         color:rgb(77,85,93);
		}
	}
   }
}
</style>


