<template>
<div  class="goodsRatingsDetail">
    <div class="content-date-name-img">
        <div class="content-date">
        	{{date}}
    	</div>
        <div class="content-name-img">
		    <div>{{goodsRatingsTemplate.username}}</div>
			<div>
				<img :src="goodsRatingsTemplate.avatar" alt="">
			</div>
		</div>
     </div>
     <div class="zan_bu_zan_text">
     	<div class="zan_bu_zan">
			<i class="iconfont icon-damuzhion" v-if="goodsRatingsTemplate.rateType"></i>
			<i class="iconfont icon-down" v-if="!goodsRatingsTemplate.rateType"></i>
		</div>
		<div class="content-text">{{goodsRatingsTemplate.text}}</div>
     </div>
    <div class="ratingsDetailLine"></div>
</div>
</template>
<script>
	export default{
       props:["goodsRatingsTemplate"],
       computed:{
       	date(){
       		var date=new Date(this.goodsRatingsTemplate.rateTime);
       		return date.getFullYear()+"-"+(date.getMonth()+1>9?date.getMonth()+1:"0"+(date.getMonth()+1))+"-"+(date.getDate()>9?date.getDate():"0"+date.getDate())+" "+(date.getHours()>9?date.getHours():"0"+date.getHours())+":"+(date.getMinutes()>9?date.getMinutes():"0"+date.getMinutes())+":"+(date.getSeconds()>9?date.getSeconds():"0"+date.getSeconds());
       	},
       }
	}
</script>
<style lang="less">
.goodsRatingsDetail{
	padding:0 0.36rem; 
	.content-date-name-img{
		padding:0.32rem 0 0;  
		display: flex;
		justify-content:space-between;
		.content-date{
			font-size:0.2rem;
			line-height:0.24rem;
			color:rgb(7,17,27);
		}
		.content-name-img{
			display: flex;
			div:nth-child(1){
               	font-size:0.2rem;
			    line-height:0.24rem;
			    color:rgb(7,17,27);
			}
			div:nth-child(2){
				img{
					width:0.24rem;
					border-radius:50%;
				}
				
			}
		}
	}
   .zan_bu_zan_text{
   	    display: flex;
   	    align-items:center;
	   	margin-top:0.12rem;
	   	padding:0 0 0.32rem;
   	    .zan_bu_zan{
   	    	.icon-damuzhion,.icon-down{
   	    		font-size: 0.24rem;
	   	    	line-height:0.48rem;
   	    	}
   	    	.icon-damuzhion{
   	    		color:rgb(0,160,220);
   	    	}
   	    	.icon-down{
				color:rgb(147,153,159);
   	    	}
   	    }
   	    .content-text{
		font-size:0.24rem;
		line-height:0.32rem;
		color:rgb(7,17,27);
	   }
   }
   .ratingsDetailLine{
		background-color:rgba(7,17,27,0.1);
        height:0.02rem;
		}
	}
</style>


