<template>
<div id="icons">
        <div v-for="(value,key) in icons" :key="key">
            <div :class="types[value.type]">
            </div>
            <div class="description" >
            	{{value.description}}
            </div>
        </div>
 </div>
</template>
<script>
    export default{
        data(){
            return {
                types: ["decrease", "discount", "guarantee", "invoice", "special"]
            };
        },
        props:["icons"],
    }
</script>
<style lang="less" scoped>
#icons{
   	  div{
   	  	display:flex;
	   	align-items:center;
	    .decrease{
	        background:url(../../static/imgs/decrease.png) no-repeat;
		    background-size:cover; 
	    }
	    .discount{
	        background:url(../../static/imgs/discount.png) no-repeat;
	        background-size:cover; 
	    }
	    .guarantee{
	        background:url(../../static/imgs/guarantee.png) no-repeat;
			background-size:cover; 
	    }
	    .invoice{
	        background:url(../../static/imgs/invoice.png) no-repeat;
	        background-size:cover; 
	    }
	    .special{
	        background:url(../../static/imgs/special.png) no-repeat;
	        background-size:cover; 
	    }
	    .description{
	    	font-size:0.24rem;
	    	font-weight:200;
	    	line-height:0.32rem;
	    	/* color:rgb(255,255,255); */
	    }
	}
}
 </style>


