<template>
	<div class="wrap">
		<div class="header_wrap">
			<!-- 这是模糊的背景 -->
			<img :src="getSellers.avatar" alt="" class="background">
			<!-- 这是header部分 -->
			<div  class="header">
				<!-- 左侧图片 -->
				<div class="left">
					<img :src="getSellers.avatar" alt="" class="avatar">
				</div>
				<!-- 右侧文字 -->
				<div class="right">
					<div class="header_top">
						<img src="../../static/imgs/brand.png" class="brand" alt="">
						<p class="title">{{getSellers.name}}</p>
					</div>
					<div>
						<span class="header_middle">{{getSellers.description}}</span>
						<span class="header_middle">/{{getSellers.deliveryTime}}分钟送达</span>
						</div>
					<div class="header_bottom">
						<div class="header_bottom-left">
							<img src="../../static/imgs/decrease.png" alt="" class="decrease">
							<div class="decrease_info" v-if="getSellers.supports">
								{{getSellers.supports[0].description}}
							</div>
						</div>
						<!-- 5个 -->
						<div class="sum" @click="show"  v-if="getSellers.supports">
						<span class="sum_text ">{{getSellers.supports.length}}个</span>
						<span class="sum_gt">&gt;</span>
						</div>
					</div>
				</div>
			</div>
			<div class="bulletin">
				<div class="bulletin_left">
					<img src="../../static/imgs/bulletin.png" class="bulletin_img"alt="">
				</div>
				<div class="bulletin_middle">
					{{getSellers.bulletin}}
				</div>
				<!-- 公告 -->
				<div class="bulletin_right">
					<span>&gt;</span>
				</div>
			</div>
		</div>
		<transition enter-active-class="animated flipInY"  leave-active-class="animated flipOutY"> 
		<detail v-if="showOrHiddenDetail"></detail>
		</transition>
	</div>
</template>

<script>
	import {mapGetters} from "vuex"
	import detail from "./detail"
	export default {
		data(){
			return{
				type:0
			}
		},
		computed:{
			...mapGetters([
				'getSellers',
				"showOrHiddenDetail"
			]),
		},
		methods:{
			show(){
				this.$store.dispatch('showDetail');
			}
		},
		components:{
			detail,
		},
		created(){
			this.$store.dispatch('getSellers');
		}
	}
</script>

<style lang="less" scoped>
.wrap{
	overflow: hidden;
	.header_wrap{
		position: relative;
		.background{
			position: absolute;
			top:0;
			bottom:0;
			left:0;
			right:0;
			width: 100%;
			height: 100%;
			z-index: -10;
			filter:blur(6px);
		}
		.header{
			height:1.28rem;
			background:rgba(7,17,27,0.5);
			blur:10px;
			padding:0.48rem 0.24rem 0.36rem 0.48rem;
			display: flex;
			.left{
				margin-right: 0.32rem;
				.avatar{
					width:1.28rem;
					height: 1.28rem;
					border-radius: 0.04rem;
				}
			}
			/*
		    右侧自适应
			*/
			.right{
				flex:1;
				/*
			    右侧上面
				*/
				.header_top{
					display: flex;
					margin-bottom: 0.16rem;
					margin-top:0.04rem;
					.brand{
						width: 0.6rem;
						height:0.35rem;
						margin-right: 0.12rem
					}
					.title{
						font-size: 0.32rem;
						color: rgb(255,255,255);
						font-weight: bold;
						line-height: 0.36rem;
					}
				}
				/*
				右侧中间
				*/
				.header_middle{
					font-size: 0.24rem;
					color: rgba(255,255,255,0.8);
					line-height: 0.24rem;
					font-weight:200;
				}
				/*
			    右侧下面
				*/
				.header_bottom{
					display: flex;
					margin-bottom:0.04rem;
					justify-content:space-between;
					/*
				    右侧下面左侧
					*/
					.header_bottom-left{
						display: flex;
						align-items:center;
						/*
					    右侧下面左侧---减
						*/
						.decrease{
							width: 0.242rem;
							height:0.242rem;
							margin-right: 0.1rem;
						}
						/*
					    右侧下面左侧---减的信息
						*/
						.decrease_info{
							color:rgba(255,255,255,0.8);
							line-height: 0.24rem;
							font-size:0.2rem;
							font-weight:200;
						}
					}
				
					/*
				    右侧下面左侧---个数
					*/
					.sum{
						position:relative;
						bottom:0.12rem;
						text-align: center;
						padding:0.14rem 0.16rem;
						background:rgba(0,0,0,0.2);
						border-radius: 0.36rem;
						.sum_text,.sum_gt{
							font-size: 0.2rem;
							color: rgba(255,255,255,0.8);
							line-height: 0.24rem;
							font-weight:200;
						}
						.sum_text{
							margin-left:0.04rem;
						}
					}
				}
			}
		}
		.bulletin{
			display: flex;
			height: 0.56rem;
			background:rgba(7,17,27,0.6);
			align-items:center;
			.bulletin_left{
				padding:  0 0.08rem 0 0.24rem;
				.bulletin_img{
					width: 0.5rem;
				}
			}
			.bulletin_middle{
				overflow: hidden;
				text-overflow :ellipsis;
				white-space: nowrap;
				font-size:0.2rem;
				color:rgb(255,255,255);
			}
			.bulletin_right{
				margin:0 0.24rem 0 0.08rem;
				font-size:0.2rem;
				color:rgb(255,255,255);
			}

		}
	}

}

	
</style>


