<template>
<div class="detailBackground" v-if="showOrHiddenDetail">
	<!-- 顶部商家信息 -->
    <div class="sticky_footer">
         <div class="content">
			<div class="seller_title">
				{{getSellers.name}}
			</div>
			<!-- 引入星星组件 -->
			<!-- 这里需要传递数据 -->
			<stars :stars="getSellers.score"></stars>	
			<!-- 优惠信息 -->
			<!-- 分割-->
			<div class="split">
				<div class="line"></div>
				<div class="info">优惠信息</div>
				<div class="line"></div>
			</div>
				<!-- 引入icons组件 -->
				<!-- 这里需要传递数据 -->
			<icons :icons="getSellers.supports"></icons>
			<!-- 商家公告 -->

			<!-- 分割-->
			<div class="split">
				<div class="line"></div>
				<div class="info">商家公告</div>
				<div class="line"></div>
			</div>
	        <!-- 內容-->
		    <div class="seller_notice_content">
			    <p>{{getSellers.bulletin}}</p>
			<!-- 关闭按钮 -->
			</div>
		</div>
		<div class="close">
			<i class="iconfont icon-X"  @click="hiddenDetail"></i>
		</div>
	</div>
</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import stars from "./stars"
	import icons from "./icons"
	export default{
		computed:{
			...mapGetters([
				"showOrHiddenDetail",
				"getSellers",
			]),

		},
		components:{
			stars,
			icons
		},
		methods:{
			hiddenDetail(){
				this.$store.dispatch("hiddenDetail")
			}
		}
	}
</script>
<style lang="less">
.detailBackground{
		position: fixed;
		background:rgba(0,0,0,0.7);
		top:0;
		bottom: 0;
		left:0;
		right:0;
		z-index:9999;
		text-align:center;
		padding:1.28rem 0.72rem 0 0.72rem;
		.sticky_footer{
			height:100%;
			display:flex;
			flex-direction:column;
			.content{
				flex:1;
				.seller_title{
					font-size:0.32rem;
				}
				/*
				星星
				*/
                .stars{
                	margin:0.32rem 1.12rem 0.56rem;
                	img{
                		height:0.48rem;
                	}
                }
				/*
				分割线
				*/
				.split{
					display:flex;
					align-items:center;
					justify-content:space-between;
					.line{
						background-color:rgba(255,255,255,0.2);
						width:2.24rem;
						height:0.04rem;
					}
					.info{
						font-size:0.28rem;
						font-weight:700;
						color:rgb(255,255,255);
					}
				}
				#icons{
			    	padding:0.24rem 0.24rem 0.56rem ;
					.decrease,.discount,.guarantee,.invoice,.special{
						width:0.32rem;
						height:0.32rem;
						margin:0.24rem 0.12rem 0 0;
						}
					.description{
						    margin:0.24rem 0 0;
							font-size:0.24rem;
							line-height:0.24rem;
						    font-weight:200;
						    color: rgb(255,255,255);
						}
				}
				/*
				商家公告
				*/
			    .seller_notice_content{
					text-align:left;
					font-size:0.24rem;
					color:rgb(255,255,255);
					line-height:0.48rem;
					padding:0.48rem 0.24rem 0;
				}
			}
		}
		.close{
			flex:0 0 auto;
            margin-bottom:0.64rem;
			.icon-X{
				font-size:0.64rem;
				color:rgba(255,255,255,0.5);
			}
		}
	}
</style>


