import types from '../types.js'
import axios from 'axios'
// 相当于.vue里面的data
const state = {
	goods:[],
	cartGoods:[],
	showOrHiddenGoodsDetail:false,
	showOrHiddenCartDetail:false,
}
// 返回数据
const getters = {
	getGoods(state){
		return state.goods;
	},
	showOrHiddenGoodsDetail(state){
		return state.showOrHiddenGoodsDetail;
	},
	showOrHiddenCartDetail(state){
		return state.showOrHiddenCartDetail;
	}
}

const actions = {
	getGoods({commit}){
		axios.get("../../../static/data.json").then( resp => {
			if( resp.data.errno === 0 ){
				commit(types.GET_GOODS,resp.data.goods)
			}
		})
	},
	showGoodsDetail({commit}){
		commit(types.SHOW_GOODS_DETAIL);
	},
	hiddenGoodsDetail({commit}){
		commit(types.HIDDEN_GOODS_DETAIL);
	},
	showCartGoodsDetail({commit}){
		commit(types.SHOW_CART_GOODS_DETAIL);
	},
	hiddenCartGoodsDetail({commit}){
		commit(types.HIDDEN_CART_GOODS_DETAIL);
	},

}
// 执行具体动作
const mutations = {
	[types.GET_GOODS](state,data){
		state.goods = data;
	},
	[types.SHOW_GOODS_DETAIL](state){
		state.showOrHiddenGoodsDetail=true;
	},
	[types.HIDDEN_GOODS_DETAIL](state){
		state.showOrHiddenGoodsDetail=false;
	},
	[types.SHOW_CART_GOODS_DETAIL](state){
		state.showOrHiddenCartDetail=true;
	},
	[types.HIDDEN_CART_GOODS_DETAIL](state){
		state.showOrHiddenCartDetail=false;
	},

}
export default {
	state,
	getters,
	actions,
	mutations
}



