import types from '../types.js'
import axios from 'axios'
// 相当于.vue里面的data
const state = {
	ratings:[],
}
// 返回数据
const getters = {
	getRatings(state){
		return state.ratings;
	},
}

const actions = {
	getRatings({commit}){
		axios.get("../../../static/data.json").then( resp => {
			if( resp.data.errno === 0 ){
				commit(types.GET_RATINGS,resp.data.ratings)
			}
		})
	}
}
// 执行具体动作
const mutations = {
	[types.GET_RATINGS](state,data){
		state.ratings = data;
	}
}
export default {
	state,
	getters,
	actions,
	mutations
}




