<template>
<div  class="ratingsTemplate">
	<div  class="ratingsTemplateTop">
		<div class="img">
			<img :src="ratingsTemplate.avatar" alt="">
		</div>
		
        <div class="content">
            <div class="content-name-date">
	        	<div>{{ratingsTemplate.username}}</div>
	        	<div>{{date}}</div>
        	</div>
        	<div class="content-score-time">
        		<stars :stars="ratingsTemplate.score"></stars>	
        		<div class="time" v-if="ratingsTemplate.deliveryTime">{{ratingsTemplate.deliveryTime}}分钟送达</div>	
        	</div>
        	<div class="content-text">{{ratingsTemplate.text}}</div>
        	<div></div>
        </div>
     </div>
        <div class="ratingsTemplateLine"></div>
	</div>
</template>
<script>
    import stars from "./stars"
	export default{
       props:["ratingsTemplate"],
       components:{
       	stars,
       },
       computed:{
       	date(){
       		var date=new Date(this.ratingsTemplate.rateTime);
       		return date.getFullYear()+"-"+(date.getMonth()+1>9?date.getMonth()+1:"0"+(date.getMonth()+1))+"-"+(date.getDate()>9?date.getDate():"0"+date.getDate())+" "+(date.getHours()>9?date.getHours():"0"+date.getHours())+":"+(date.getMinutes()>9?date.getMinutes():"0"+date.getMinutes())+":"+(date.getSeconds()>9?date.getSeconds():"0"+date.getSeconds());
       	},
       }
	}
</script>
<style lang="less">
.ratingsTemplate{
	.ratingsTemplateTop{
		padding:0.36rem;
		display:flex;
		.img{
			img{
			width:0.56rem;
			border-radius:50%;
			}
		}
		.content{
			flex:1;
			margin-left:0.24rem;
			.content-name-date,.content-score-time{
				display:flex;
				align-items:center;
			}
			.content-name-date{
				justify-content:space-between;
				div:nth-child(1){
					font-size:0.2rem;
					line-height:0.24rem;
					color:rgb(7,17,27);

				}
				div:nth-child(2){
					font-size:0.2rem;
					line-height:0.24rem;
					font-weight:200;
					color:rgb(147,153,159);
				}
			}
			.content-score-time{
				/*
				星星
				*/
                .stars{
                	img{
                		height:0.2rem;
                	}
                }
                .time{
                	font-size:0.2rem;
					line-height:0.24rem;
					font-weight:200;
					color:rgb(147,153,159);
					margin-left:0.12rem;
                }
			}
			.content-text{
				margin-top:0.12rem;
				font-size:0.2rem;
				line-height:0.36rem;
				color:rgb(7,17,27);
			}
		}

	}
   .ratingsTemplateLine{
		background-color:rgba(7,17,27,0.1);
        height:0.02rem;
        margin:0 0.36rem;
		}

	}
</style>


