<template>
<div class="cartDetailBackground" v-if="showOrHiddenCartDetail">
	<div class="cart-goods">
		 <div class="cart">
		 	<div>
		 		购物车
		 	</div>
		 	<div @click="clearAll">
		 		清空
		 	</div>
		 </div>
		 <div class="cartgoods" ref="cartgoods">
			    <div>
			 	  <div class="cartgood" v-for="(value,key) in getCartGoods" :key="key">
				 	  <div class="name">
				        {{value.name}}
				 	  </div>
				 	  <div class="price-plus-or-minus">
					 	  <div class="totalPrice">
                               <span>&yen;{{value.price*value.counts}}</span>
					 	 </div>
					 	  <plus-or-minus :value="value"></plus-or-minus>
				 	  </div>
				   </div>
		      </div>
		 </div>
	</div>
</div>
</template>

<script>
	import {mapGetters} from "vuex"
    import plusOrMinus  from "./plusOrminus"
	import BScroll from "better-scroll"
	export default{
		computed:{
			...mapGetters([
				"showOrHiddenCartDetail",
				"getGoods"
			]),
			getCartGoods(){
            	var getCartGoods=[];
            	this.getGoods.forEach((values)=> {
            		values.foods.forEach((value)=>{
	            		if(value.counts){
	            			getCartGoods.push(value);
	            		}
            		}) 
            	});
            	return getCartGoods;
            },

		},
		components:{
			plusOrMinus
		},
		created(){
			this.$nextTick(() => {
           /* 在这个函数中调用以防内容还未加载完就执行，获取不到元素的高度导致无法滑动*/
            this.initScroll();
         });
		},
		methods:{
			clearAll(){
				// 清空
				this.getGoods.forEach((values)=> {
            		values.foods.forEach((value)=>{
	            		if(value.counts){
	            			this.$delete(value,"counts");
	            		}
            		}) 
            	});


			},
			initScroll () {  
                setTimeout(() =>{
                	console.log(this.$refs);
                    var cartgoods= new BScroll(this.$refs.cartgoods,{
                        click:true,
                     });
                },0);
             }
		}
	}
</script>
<style lang="less">
.cartDetailBackground{
		position: fixed;
		background:rgba(0,0,0,0.7);
		top:0;
		bottom: 0.92rem;
		left:0;
		right:0;
		z-index:1;
		text-align:center;
		padding:1.28rem 0.72rem 0 0.72rem;
		.cart-goods{
			position:fixed;
			bottom: 0.92rem;
			left:0;
			right:0;
			background: rgb(255,255,255);
			.cart{
				z-index:9;
				height:0.8rem;
				padding:0 0.36rem;
				align-items:center;
				display: flex;
				justify-content:space-between;
				background-color: #f3f5f7;
				border-top:0.03rem solid rgba(7,17,27,0.1);
				border-bottom:0.03rem solid rgba(7,17,27,0.1);
				>div:nth-child(1){
					font-size:0.28rem;
					font-weight: 200;
					color:rgb(7,17,27);
					line-height: 0.8rem;
				}
				>div:nth-child(2){
					font-size:0.24rem;
					color:rgb(0,160,220);
					line-height: 0.8rem;
				}
			}
			.cartgoods{
				max-height:4rem;
				overflow: hidden;
				>div:nth-child(1){
					padding:0rem 0.36rem;
					.cartgood{
						border-bottom:0.02rem solid rgba(7,17,27,0.1);
						display: flex;
						justify-content:space-between;
						align-items:center;
					    .name{
							font-size: 0.28rem;
							line-height: 0.48rem;
							color:rgb(7,17,27);
						}
						.price-plus-or-minus{
							display: flex;
							align-items:center;
							.totalPrice{
								padding:0rem 0.24rem 0 0.36rem;
								>span:nth-child(1),>span:nth-child(2){
									color:rgb(240,20,20);
									line-height: 0.8rem;
								}
								>span:nth-child(1){
									font-size:0.2rem;
									font-weight:normal;
								}
								>span:nth-child(2){
									font-size:0.28rem;
									font-weight:700;
								}
							}
						}
					}
				}
			}
		}
	}
</style>


