<template>
<div class="rantings" ref="ratings">
    <div>
        <div class="header">
            <div class="header_left">
                  <div class="header_left_score">{{getSellers.score}}
                  </div>
               <!-- 这里面要处理传递数据 -->
                   <div class="header_left_title">
                      综合评分
                   </div>
                   <div class="header_left_compare">
                      高于周边商家{{getSellers.rankRate}}%
                   </div>
            </div>
            <div class="header_right">
                   <div class="header_right_service_score">
                       <div class="header_right_title">服务态度</div>
                       <stars :stars="getSellers.serviceScore"></stars>
                       <div class="header_right_score">{{getSellers.serviceScore}}</div>
                  </div>
               <!-- 这里面要处理传递数据 -->
                   <div class="header_right_food_score">
                        <div class="header_right_title">食物评分</div>
                        <stars :stars="getSellers.foodScore"></stars>
                        <div class="header_right_score">{{getSellers.foodScore}}</div>
                   </div>
                   <div class="header_right_time">
                        <div class="header_right_title">送达时间</div>
                        <div>{{getSellers.deliveryTime}}分钟</div>
                   </div>
            </div>
        </div>   
        <div class="split"></div>  
        <!-- 父组件从子组件取值
         -->
        <click-and-select-ratings :goodsRatings="getRatings" @send="sendData">
            <span slot="all">全部</span>
            <span slot="good">满意</span>
            <span slot="bad">不满意</span>
         </click-and-select-ratings>
        <ratings-template v-for="(value,key) in isClickSelect?getSelectRatings:getRatings" :key="key" :ratingsTemplate="value"></ratings-template>
    </div>
 </div>
</template> 

<script>
  import BScroll from "better-scroll"
  import stars from "./stars"
  import ratingsTemplate from "./ratingsTemplate"
  import clickAndSelectRatings from "./clickAndSelectRatings"
  import {mapGetters} from "vuex"
	// new BScroll()
  export default{
     data(){
        return{
            ratings:{},
            isClick:false,
            isClickSelect:false,
            getSelectRatings:[],
        }
     },
        // 计算属性是比较特殊的
     computed:{
      //  也是异步得判断
     	...mapGetters(["getRatings","getSellers"]),
   
     },
     // 路由跳转是被销毁了
     created(){
     	  this.$store.dispatch("getRatings");
        this.$nextTick(() => {
       /* 在这个函数中调用以防内容还未加载完就执行，获取不到元素的高度导致无法滑动*/
        this.initScroll();
        });
     },
     methods:{
        initScroll () {  
            setTimeout(() =>{
                this.ratings= new BScroll(this.$refs.ratings,{
                    click:true,
                  });
            },0);
        },
        sendData(data){
          this.isClickSelect=true;
          this.getSelectRatings=data;
          this.$nextTick(()=>{
          this.ratings.refresh();
         });
        }
     },
     components:{
        stars,
        ratingsTemplate,
        clickAndSelectRatings
     },
 }
</script>
<style lang="less">
 .rantings{
    position: absolute;
    top:3.48rem;
    bottom:0.92rem;
    width:100%;
    overflow:hidden;
    .header{
        display: flex;
        align-items:center;
        padding:0.36rem 0.48rem;
        .header_left{
            /*
            平均分
            */
            flex:1;
            text-align: center;
            border-right: 0.03rem solid rgba(7,17,27,0.1);
            .header_left_score{
                font-size: 0.48rem;
                line-height: 0.56rem;
                color: rgb(255,153,0);
            }
            .header_left_title{
                font-size: 0.24rem;
                line-height: 0.24rem;
                color: rgb(7,17,27);
                margin-top: 0.12rem;
            }
            .header_left_compare{
                font-size: 0.2rem;
                line-height: 0.2rem;
                color: rgba(7,17,27,0.6);
                margin: 0.16rem 0 0.12rem;
            }

        }
        .header_right{
            /*
            平均分
            */
            flex:1.72;
            padding:0 0 0 0.48rem;
            .header_right_service_score,.header_right_food_score,.header_right_time{
                display: flex;
                align-items:center;
                   /*
                    星星
                    */
                .stars{
                    img{
                        height:0.36rem;
                    }
                }
                .header_right_title{
                    font-size: 0.24rem;
                    line-height: 0.36rem;
                    color: rgb(7,17,27);
                    margin-right: 0.24rem;
                }
            }
            .header_right_service_score,.header_right_food_score{
                font-size: 0.24rem;
                line-height: 0.36rem;
                color: rgb(255,153,0);
                margin-left:0.24rem;
            }
            .header_right_time{
                font-size: 0.24rem;
                line-height: 0.36rem;
                color: rgb(147,153,159);
                margin-left:0.24rem;

            }
        }
    }
    .split{
        height:0.32rem;
        border-top: 0.03rem solid rgba(7,17,27,0.1);
        border-bottom: 0.03rem solid rgba(7,17,27,0.1);
        background: #f3f5f7;
    }
    .ratings{
      >div:nth-child(1){
        display:none;
      }
    }
 }
</style>


