<template>
	<div class="stars">
	  <!-- 满星 -->
	  <div v-for="(value,key) in 5" :key="key">
	  <img src="../../static/imgs/star_on.png" alt="" v-if="value<=stars">
	  <!-- 半星 -->
	  <img src="../../static/imgs/star_half.png" alt="" v-if="value>stars&&value<stars+1">
	  <!-- 目的只要一次 -->
	  <!-- 空星 -->
	  <img src="../../static/imgs/star_off.png" alt="" v-if="value>=stars+1">
	  </div>
	</div>
</template>
<script>
	export default{
		props:["stars"]
	}
</script>
<style lang="less" scoped>
  .stars{
  	display:flex;
  	justify-content:space-between;
  }
</style>


