import types from "../types.js"
import axios from "axios"
const state = {
	sellers:{},
	showOrHiddenDetail:false,
	}
const getters = {
	getSellers(state){
		return state.sellers;
	},
	showOrHiddenDetail(state){
		return state.showOrHiddenDetail;
	}
}
const actions = {
	getSellers({commit}){
		axios.get("../../../static/data.json").then((resp)=>{
			if(resp.data.errno===0){
				commit(types.GET_SELLERS,resp.data.seller);
			}
		})
	},
	showDetail({commit}){
		commit(types.SHOW_DETAIL)
	},
	hiddenDetail({commit}){
		commit(types.HIDDEN_DETAIL)
	}
}
const mutations = {
	[types.GET_SELLERS](state,sellers){
		state.sellers=sellers;
	},
	[types.SHOW_DETAIL](state){
		state.showOrHiddenDetail=true;
	},
	[types.HIDDEN_DETAIL](state){
		state.showOrHiddenDetail=false;
	}
}

export default {
	state,
	getters,
	mutations,
	actions
}


