<template>
	<div class="sellers" ref="sellers">
        <!-- 商家信息头部 -->
      <div>
		<div class="header">
            <div class="header_top">
                <div class="header_top_left">
                    <div class="header_top_left_title">{{getSellers.name}}</div>
                   <!-- 这里面要处理传递数据 -->
                    <div class="header_top_left_ratings_sale">
                        <stars :stars="getSellers.score"></stars>
                        <div class="header_top_left_num">
                            (661)
                        </div>
                        <div class="header_top_left_sellCount">
                           月售{{getSellers.sellCount}}单
                        </div>
                    </div>
                </div>
                <div class="header_top_right">
                    <div  class="iconfont_like" @click="changeISCollect($event)">
                         <i class="iconfont icon-xin" :class="{'isCollect':isCollect,'isNotCollect':!isCollect}" ></i>
                    </div>
                    <div>
                        <span class="collect">{{isCollect?"已收藏":"收藏"}}</span>
                    </div>
                </div>
            </div> 
            <div class="header_bottom">
                <div class="avg">
                    <div class="header_bottom_info">起送价</p></div>
                    <div class="header_bottom_amount_unit">
                    <span class="header_bottom_amount">{{getSellers.minPrice}}</span>
                    <span class="header_bottom_unit">元</span>
                    </div>
                </div>
                <div class="avg">
                    <div class="header_bottom_info">商家配送</div>
                    <div class="header_bottom_amount_unit"><span class="header_bottom_amount">{{getSellers.deliveryPrice}}</span>
                    <span class="header_bottom_unit">元</span></div>
                </div>
                <div  class="avg">
                    <div class="header_bottom_info">平均配送时间</div>
                    <div class="header_bottom_amount_unit"> 
                    <span class="header_bottom_amount">{{getSellers.deliveryTime}}</span>
                    <span class="header_bottom_unit">分钟</span>		
                </div>
                </div>
            </div>        
        </div>
        <!-- 分隔div -->
        <div class="split"></div>
        <!-- 公告与活动 -->
        <div class="adv">
            <div class="adv_title">公告与活动</div>
            <div class="adv_content">{{getSellers.bulletin}}</div>
            <icons :icons="getSellers.supports"></icons>
        </div>
        <!-- 分隔div -->
        <div class="split"></div>
        <!-- 商家实景 -->
        <div class="view">
              <div class="view_title">商家实景</div>
              <div  class="view_pics" ref="imgs">
                  <div style="white-space:nowrap">
                    <img v-for="(value,key) in getSellers.pics" :key="key" :src="value" alt="">
                  </div>
              </div>
        </div>
        <!-- 分隔div -->
        <div class="split"></div>
        <!-- 商家信息 -->
        <div class="info">
            <div class="info_title">商家信息</div>
            <div class="info_detail" v-for="v in getSellers.infos">
                <div class="info_detail_text">{{v}}</div>
                <div class="line"></div>
            </div>
        </div>
	</div>
</div>
</template>
<script>
    import {mapGetters} from "vuex"
    import BScroll from "better-scroll"
    import stars from "./stars"
    import icons from "./icons"
    export default{
        data(){
          return{
            isCollect:false,
          }
        },
    	computed:{
    		...mapGetters([
    			"getSellers"
    		])
    	},
        created(){
           this.$nextTick(() => {
           /* 在这个函数中调用以防内容还未加载完就执行，获取不到元素的高度导致无法滑动*/
            this.initScroll();
         });
        },
        methods:{
            changeISCollect(e){
               if(!e._constructed){
                return;
               }
                this.isCollect=!this.isCollect;
            },   
            initScroll () {  
                setTimeout(() =>{
                    var sellers= new BScroll(this.$refs.sellers,{
                        probeType:3,
                        click:true,
                      });
                    var imgs=new BScroll(this.$refs.imgs,{
                        probeType:3,
                        scrollX:true,
                        click:true,
                      });
               
                },0);
             }
        },
        components:{
            stars,
            icons
        },
    }
</script>
<style lang="less">
.sellers{
	position: absolute;
    top:3.48rem;
    bottom:0.92rem;
    width:100%;
	overflow:hidden;
    /*
    头部信息
    */
    .header{
        .header_top{
            padding: 0.36rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            display: flex;
            /*
            上面的左边的
            */
            .header_top_left{
                flex:1;
                .header_top_left_title{
                    font-size: 0.28rem;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                }
                .header_top_left_ratings_sale{
                    display: flex;
                    align-items:center;
                    margin:0.3rem 0 0;
                    /*
                    星星
                    */
                    .stars{
                        img{
                            height:0.36rem;
                        }
                    }
                    .header_top_left_num,.header_top_left_sellCount{
                         font-size:0.2rem;
                         line-height:0.36rem;
                         color:rgb(77,85,93);
                    }
                    .header_top_left_num{
                         margin-left:0.16rem;
                    }
                    .header_top_left_sellCount{
                         margin-left:0.24rem;
                    }
                }
            }
            .header_top_right{
                 width:0.8rem;
                 text-align:center;
                 .iconfont_like{
                    margin-bottom:0.08rem;
                    font-size:0.48rem; 
                    line-height: 0.48rem;
                    .isCollect{
                        color:rgb(240,20,20); 
                    }
                    .isNotCollect{
                        color:rgb(7,17,27); 
                    }
                 }
                 .collect{
                    font-size:0.2rem;
                    line-height:0.36rem;
                    color:rgb(77,85,93);
                 }

            }
        }
        .header_bottom{
            display: flex;
            padding: 0.36rem;
            align-items:center;
            .avg{
                flex:1;
                text-align: center;
                border-right: 0.02rem solid rgba(7,17,27,0.1);
                .header_bottom_info{
                    font-size:0.2rem;
                    color:rgb(147,153,159);
                    line-height:0.2rem;
                }
                .header_bottom_amount_unit{
                	margin-top:0.08rem;
                	font-weight:200;
                	color:rgb(7,17,27);
	                .header_bottom_amount{
	                	font-size:0.48rem;
	                }
	                .header_bottom_unit{
	                	font-size:0.2rem;
	                }
                }
            }
            .header_bottom_right{
                 border-right: none;
            }
        }
    }
    .adv{
        padding:0.36rem 0.36rem 0 ;
        .adv_title{
            font-size: 0.28rem;
            color:rgb(7,17,27);
            line-height:0.28rem;
        }
        .adv_content{
            line-height:0.48rem;
            padding:0.16rem 0.24rem 0.32rem;
            color:rgb(240,20,20);
            font-size:0.24rem;
        }
        /*
        icons的设置
        */
        #icons{
            >div{
                border-top:0.02rem solid rgba(7,17,27,0.1);
                padding: 0.32rem 0.24rem;
            }
            .decrease,.discount,.guarantee,.invoice,.special{
                width:0.32rem;
                height:0.32rem;
                margin: 0 0.12rem 0 0.24rem;
                }
            .description{
                    font-size:0.24rem;
                    line-height:0.24rem;
                    font-weight:200;
                }
            }
        }
     /*
    商家实景
    */
    .view{
        padding:0.36rem 0 0.36rem 0.36rem;
        .view_title{
            font-size: 0.28rem;
            color:rgb(7,17,27);
            line-height:0.28rem;
        }    
        .view_pics{
            display:flex;
            img{
                margin:0.24rem 0.12rem 0 0;
                height:1.8rem;
                width:2.4rem;
            }

        }
    }
    .split{
        height:0.32rem;
        border-top: 0.03rem solid rgba(7,17,27,0.1);
        border-bottom: 0.03rem solid rgba(7,17,27,0.1);
        background: #f3f5f7;
    }
    .info{
    	padding:0.36rem 0.36rem 0;
    	.info_title{
    		font-size: 0.28rem;
            color:rgb(7,17,27);
            line-height:0.28rem;
    	}
    	.info_detail{
            .info_detail_text{
            	padding:0.32rem 0.24rem;
                font-size: 0.24rem;
                font-weight:200;
	            color:rgb(7,17,27);
	            line-height:0.32rem;
            }
            .line{
        		background-color:rgba(7,17,27,0.1);
		        height:0.02rem;
            }
    	}

    }
}
</style>



