<template>
	<div class="goods">
        <div class="goods-left-right-flex">
         <div ref="goodsLeft" class="goods-left">
           <!-- 注意使用bs时滑动的元素一定要有个容器包裹 -->
            <ul>
            <!-- 空对象 v-for循环不出错  无需判断 -->
               <li v-for="(value,key) in getGoods" :key="key" @click="selectMenu(key,$event)"  :class="{'current':currentIndex===key}">
                    <span>
                    {{value.name}}
                    </span>
                </li>
            </ul>
         </div>
	     <div ref="goodsRight" class="goods-right">
            <div>
                <!-- 注意使用bs时滑动的元素一定要有个容器包裹 -->
                <!-- 空对象 v-for循环不出错  无需判断 -->
                <!-- 每一个菜单下的食物列表-->
                <div v-for="(value,listKey) in getGoods" :key="listKey" class="goodsList">
                    <div class="line-type-title">
                        <div class="line"></div>
                        <div class="type-title">
                          {{value.name}}
                        </div>
                    </div>
                    <div v-for="(value,detailKey) in value.foods" :key="detailKey" class="info" @click="showGoodsDetail(value,$event)">
                        <div class="avater">
                        <!-- 注意空对象的属性值为undefined -->
                            <img :src="value.icon" alt=""   >  
                        </div>
                        <div class="detail">
                            <div class="name">{{value.name}}</div>
                            <div class="description">{{value.description}}</div>
                            <div class="sellCount-rating">
                                <span>月售{{value.sellCount}}</span>
                                <span>好评率{{value.rating}}%</span>
                            </div>
                            <div class="price-plus-or-minus">
                              <div class="price-oldprice">
                                   <div class="price">
                                      <span>&yen;</span>
                                      <span>{{value.price}}</span>
                                   </div>
                                   <div class="oldprice" v-if="value.oldPrice">
                                       <del>
                                          <span>&yen;</span>
                                          <span>{{value.oldPrice}}</span>
                                       </del>
                                   </div>
                                
                              </div>
                                  <plus-or-minus :value="value"></plus-or-minus> 
                            </div>
                       </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!-- 要判断是否点击了,没点击不加载 -->
    <transition enter-active-class="animated flipInY"  leave-active-class="animated flipOutY"> 
    <goods-detail :goodsDetail="goodsDetail" v-if="showOrHiddenGoodsDetail"></goods-detail>
    </transition>
 </div>
</template>

<script>
  import BScroll from "better-scroll"
  import goodsDetail from "./goodsDetail"
  import plusOrMinus from "./plusOrminus.vue"
	import {mapGetters} from "vuex"
    export default{
        data(){
            return {
                left:{},
                right:{},
                goodsList:[],
                goodsDetail:null,
                currentIndex:0,
            }
        },
        computed:{
            // 异步获取
         	...mapGetters(["getGoods","showOrHiddenGoodsDetail"]),
         },
         // 路由跳转是被销毁了
        created(){
         	this.$store.dispatch("getGoods");
            // 以前不知道的,空对象的长度为undefinded
            // console.log(".....",this.getGoods.length);
            this.$nextTick(() => {
           /* 在这个函数中调用以防内容还未加载完就执行，获取不到元素的高度导致无法滑动*/
            this.initScroll();
             });
         },
        methods:{
            initScroll () {  
                setTimeout(() =>{
                     this.left= new BScroll(this.$refs.goodsLeft,{
                        probeType:3,
                        click:true,
                      });
                     this.right= new BScroll(this.$refs.goodsRight,{
                        probeType:3,
                        click:true,
                      });
                    this.goodsList=this.$refs.goodsRight.getElementsByClassName('goodsList');
                    this.left.on('scroll', (pos) => {
                     
                    });
                    // 右侧滑动,左侧联动
                    this.right.on('scroll', (pos) => {
                        var listHeight=[];
                        for (var i = 0; i <  this.goodsList.length; i++) {
                           listHeight.push( this.goodsList[i].offsetHeight+this.goodsList[i].offsetTop);
                        }
                        for (var i = 0; i < listHeight.length; i++) {
                            if(-pos.y<listHeight[i]){
                                break;
                            } 
                        }
                       this.currentIndex=i;
                    });
                },1000);
             },
             // 点击左侧实现右侧联动
             selectMenu(index,e){
                // 
              if(!e._constructed) {
                    return;
                }
              this.currentIndex=index;
              this.right.scrollToElement(this.goodsList[index], 200);
             },
             // 点击具体食物,显示食物详情
             showGoodsDetail(goodsDetail,e){
              if(!e._constructed) {
                    return;
                }
                // 食物详情页
               this.goodsDetail=goodsDetail;
               this.$store.dispatch("showGoodsDetail");
              },
        },
        components:{
            goodsDetail,
            plusOrMinus,
        }
    }
</script>

<style lang="less" scoped>
.goods{
   .goods-left-right-flex{
           display :flex;
           position: absolute;
           bottom:0.92rem;
           top:3.48rem;
           overflow:hidden;
           .goods-left{
                 flex:0 0 1.6rem;
                 /*
                  这里还得考虑是多高？
                */
                background-color:#f3f5f7;
                ul>li{
                    height:1.08rem;
                    line-height:1.08rem;
                    padding:0rem 0.24rem;
                    span{
                        display:inline-block;
                        width:1.12rem;
                        font-size:0.24rem;
                        font-weight:200;
                        line-height:0.28rem;
                    }
                }
                ul>.current{
                    background-color: white;
                }
            }
            .goods-right{
            flex:1;
            /*
              这里还得考虑是多高？
            */
            .line-type-title{
               display:flex;
               .line{
                    height:0.52rem;
                    width:0.06rem;
                    background-color:#d9dde1;
               }
               .type-title{
                    flex:1;
                    font-size:0.24rem;
                    background-color:#f3f5f7;
                    color:rgb(147,153,159);
                    height:0.52rem;
                    line-height:0.52rem;
                    padding:0rem  0rem 0rem 0.28rem;
                }
            }
            .info{
                display:flex;
                padding:0.36rem;
                .avater{
                    margin-right:0.2rem;
                }
                .detail{
                    flex:1;
                    .name{
                        font-size:0.28rem;
                        line-height:0.28rem;
                        margin-top:0.04rem;
                        color:rgb(7,17,27);
                        height:0.52rem;
                    }
                    .description{
                        font-size:0.2rem;
                        line-height:0.2rem;
                        margin-top:0.16rem;
                        color:rgb(147,153,159);
                    }
                    .sellCount-rating{
                        font-size:0.2rem;
                        line-height:0.2rem;
                        margin-top:0.16rem;
                        color:rgb(147,153,159);
                        span:nth-child(1){
                            margin-right:0.24rem;
                        }
                    }
                    .price-plus-or-minus{
                        display: flex;
                        margin-top:0.16rem;
                        align-items:center;
                        justify-content:space-between;
                        .price-oldprice{
                            display: flex;
                            align-items:center;
                            .price{
                                margin-right:0.08rem;
                                span{
                                    color:rgb(240,20,20);
                                    line-height:0.48rem;
                                  }
                                /* nth-child 直接子 先找父  在看第几个(从1开始)  最后确定第几个是不是的 是就生效 不是就不生效了  */
                                span:nth-child(1){
                                    font-size:0.2rem;
                                    font-weight:normal;
                                }
                                span:nth-child(2){
                                    font-size:0.28rem;
                                    font-weight:700;
                                }
                            }
                            .oldprice{
                                del{
                                  span{
                                    font-size:0.2rem;
                                    color:rgb(147,153,159);
                                    line-height:0.48rem;
                                  }
                                  span:nth-child(1){
                                    font-weight:normal;
                                  }
                                  span:nth-child(2){
                                    font-weight:700;
                                  }
                                }
                            }
                        }
                      }
                    }
                  }
                }
              }
            }
</style>


